  color: #262a2e;
}
.wp-core-ui .button.focus,
.wp-core-ui .button:focus {
  border-color: #7e8993;
  color: #262a2e;
  box-shadow: 0 0 0 1px #32373c;
}
.wp-core-ui .button:active {
  border-color: #7e8993;
  color: #262a2e;
  box-shadow: none;
}
.wp-core-ui .button.active,
.wp-core-ui .button.active:focus,
