.wp-core-ui .button-secondary:focus {
  border-color: #327ff5;
  color: #063a88;
  box-shadow: 0 0 0 1px #327ff5;
}
.wp-core-ui .button-primary:hover {
  color: #fff;
}
.wp-core-ui .button-primary {
  background: #0b63e9;
  border-color: #0b63e9;
  color: #fff;
}
.wp-core-ui .button-primary:hover, .wp-core-ui .button-primary:focus {
  background: #106af4;
  border-color: #0a5dda;
  color: #fff;
}
.wp-core-ui .button-primary:focus {
  box-shadow: 0 0 0 1px #fff, 0 0 0 3px #0b63e9;
}
.wp-core-ui .button-primary:active {
  background: #0a59d1;
  border-color: #0a59d1;
